<template>
  <div class="promise">
    <div class="code-container">
      <h3>Promise</h3>
      <pre>
      function creatPromise () {
        return new Promise((resolve, reject) => {
          setTimeout(function () {
            // then
            resolve()
          }, 1000)
        })
      }

      //  1
      function creatPromise1 () {
        return new Promise((resolve, reject) => {
          let count = 0
          const END = 1
          const counter = setInterval(function () {
            count++
            if (count === END) {
              clearInterval(counter)
              console.log('promise1')
              resolve({
                msg: '1',
                results: []
              })
            }
          }, 1000)
        })
      }

      //  2
      function creatPromise2 () {
        return new Promise((resolve, reject) => {
          let count = 0
          const END = 2
          const counter = setInterval(function () {
            count++
            if (count === END) {
              clearInterval(counter)
              console.log('promise2')
              resolve('2')
            }
          }, 2000)
        })
      }

      // reject 1
      function creatPromise3 () {
        return new Promise((resolve, reject) => {
          setTimeout(function () {
            console.log('promise3')
            reject('')
          }, 1000)
        })
      }
      </pre>
      <h3></h3>
      <pre>
      // then
      addCount (async) {
        creatPromise().then(function () {
          // todo
        })
      }

      // then
      Promise.all([creatPromise1(), creatPromise2()])
      .then(function (results) {
        console.log(results)
        // results: [{
        //  msg: '1',
        //  result: []
        // }, '2']
      })

      // then
      Promise.race([creatPromise1(), creatPromise2()])
      .then(function (res) {
        console.log(res)
        console.log('<span>promise2</span>')
      })
      .catch(function (res) {
        console.log('catch')
      })

      // thenPromise
      creatPromise1()
      .then(function (res) {
        console.log(res)
        return creatPromise2()
      })
      .then(function (res) {
        console.log(res)
        return 'well done'
      })
      .then(function (msg) {
        console.log(msg) // 'well done'
      })

      // then
      // catch<span>then</span>.
      creatPromise3()
      .then(function (res) {
        console.log('promise3')
        console.log(res)
      }, function (res) {
        console.log(res)
        console.log('promise3BY~then')
      })
      .catch(function (res) {
        console.log('promise3BY~catch') // 
      })

      // catch
      creatPromise3()
      .then(function (res) {
        console.log('promise3')
        console.log(res)
      })
      .catch(function (res) {
        console.log(res)
        console.log('promise3BY~catch') // 
      })
      </pre>
    </div>
    <h3></h3>
    <div class="example">
      <p>count={{count}}</p>
      <p>{{time}}</p>
      <p>{{time1}}</p>
      <div class="margin">
        <button @click="resetData"></button>
      </div>
      <button @click="addCount(false)">count1</button>
      <button @click="addCount(true)">count1()</button>
      <button @click="addCountAfterAll()">count1(,2)all</button>
      <button @click="chainUse">promise</button>
      <p v-if="step > 0">time2time1</p>
      <p v-if="step > 1">time1</p>
      <div class="margin">
        <button @click="failSolution">promisereject</button>
        <button @click="addCountAfterAny">promiserace</button>
      </div>
      <p>
        <a href="https://www.cnblogs.com/lvdabao/p/es6-promise-1.html" target="_blank"></a>
      </p>
    </div>
  </div>
</template>

<script>
const END = 2

function creatPromise () {
  return new Promise((resolve, reject) => {
    setTimeout(function () {
      // then
      console.log('promise')
      resolve()
    }, 1000)
  })
}

//  1
function creatPromise1 (vm) {
  return new Promise((resolve, reject) => {
    const counter = setInterval(function () {
      vm.time++
      if (vm.time === END) {
        clearInterval(counter)
        console.log('promise1')
        resolve({
          msg: '1',
          results: []
        })
      }
    }, 1000)
  })
}

//  2
function creatPromise2 (vm) {
  return new Promise((resolve, reject) => {
    const counter = setInterval(function () {
      vm.time1++
      if (vm.time1 === END) {
        clearInterval(counter)
        console.log('promise2')
        resolve('2')
      }
    }, 2000)
  })
}

// reject 1
function creatPromise3 () {
  return new Promise((resolve, reject) => {
    setTimeout(function () {
      console.log('promise3')
      reject('')
    }, 1000)
  })
}

export default {
  data () {
    return {
      count: 0,
      time: 0,
      time1: 0,
      step: 0
    }
  },
  methods: {
    resetData () {
      Object.assign(this.$data, this.$options.data())
    },
    addCount (async) {
      const vm = this
      if (async) {
        creatPromise().then(function () {
          vm.count++
        })
        return
      }
      vm.count++
    },
    addCountAfterAll () {
      const vm = this
      Promise.all([creatPromise1(vm), creatPromise2(vm)])
      .then(function (results) {
        console.log(results)
        vm.count++
      })
    },
    chainUse () {
      const vm = this
      creatPromise1(vm)
      .then(function (res) {
        console.log(res)
        vm.step++
        return creatPromise2(vm)
      })
      .then(function (res) {
        console.log(res)
        vm.step++
        return 'well done'
      })
      .then(function (msg) {
        console.log(msg)
      })
    },
    failSolution () {
      // catchthenrejectcatch
      creatPromise3()
      .then(function (res) {
        console.log('promise3')
        console.log(res)
      }, function (res) {
        console.log(res)
        console.log('promise3BY~then')
      })
      .catch(function (res) {
        console.log('promise3BY~catch')
      })

      // catch
      creatPromise3()
      .then(function (res) {
        console.log('promise3')
        console.log(res)
      })
      .catch(function (res) {
        console.log(res)
        console.log('promise3BY~catch')
        console.log('thencatch,then')
      })
    },
    addCountAfterAny () {
      const vm = this
      Promise.race([creatPromise1(vm), creatPromise2(vm)])
      .then(function (res) {
        console.log(res)
        console.log('promise2')
        vm.count++
      })
      .catch(function (res) {
        console.log('catch')
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.promise{
  padding: 0.2rem 0.3rem;
  text-align: center;
  p{
    margin: 20px;
    a{
      font-size: 0.2rem;
      color: blue;
    }
  }
  .margin{
    margin: 20px;
  }
  .code-container{
    text-align: left;
  }
  .example{
    background-color: #c6dfea;
    padding: 20px 0;
  }
}
</style>



